
import _ from 'lodash'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import {mapGetters, mapActions} from 'vuex'
export default {
name: 'Records',
data() {
return {

};
},
components: {
Group,
Cell
},
mounted() {
if(this.records.length == 0)
this.getAllRecords(() => {

});
},
computed: {
...mapGetters(['records']),
},
methods: {









formatAmount({ RecordType, Amount }) {
return (RecordType == '收入' ? '+' : '-') + numberComma(Amount.toFixed(2))
},
removeRecord(index) {
var record = this.records[index];
this.$confirm('将删除此项目, 是否继续?', '提示', {
confirmButtonText: '确定',
cancelButtonText: '取消',
type: 'warning'
}).then(() => {
this.$http.post('/api/deleteRecord', { recordID: record.RecordID }).then(response => {
let res = response.body;
if (res.Success) {
this.$message({
message: '移除成功！',
type: 'success',
});
this.records.splice(index, 1);
}
})
}).catch(() => {
this.$message({
type: 'info',
message: '已取消删除'
});
});
},
...mapActions(['updateRecords', 'getAllRecords'])
}
};




